import * as timeago from 'timeago.js';
//import { toJS } from 'mobx';

class FeaturedStoriesAdapter {
  adaptData(attachAttrib,apiData, noOfItems, isPlayBtn=false, isShareable=false, imgHeight=null) {
    // console.log("FeaturedStoriesAdapter: attatchToattr", toJS(attachAttrib));
    let data;

    if(apiData?.status=='rejected'){
      return null;
    }

    data = apiData?.result?.data?.data;
    
    const storiesList = data?.rows;
    const nextPageUrl = data?.nextPageUrl;
    let items = [];
    let featuredItems = []; 
    let returnObj = [];

    try {
      for (let i = 0; storiesList && i<storiesList.length && (noOfItems?i<noOfItems:true); i++) {
        if (storiesList[i] && storiesList[i].contentImage) {
          try{
            featuredItems.push({
              id: storiesList[i].id,
              type: storiesList[i].type,
              uiType : storiesList[i].uiType,
              format : storiesList[i].format,
              subFormat : storiesList[i].subFormat
           });
          }catch(err){
            console.log("FeaturedStoriesAdapter: Story Reading Error: ",err);
          }
        }
      }
      for (let i = 0; storiesList && i<storiesList.length && (noOfItems?i<noOfItems:true); i++) {
        if (storiesList[i] && storiesList[i].contentImage) {
          try{
            const obj = {
              id: storiesList[i].id,
              title: storiesList[i].title,
              source: storiesList[i].source.sourceName,
              publishTime: storiesList[i].publishTime?timeago.format(storiesList[i].publishTime):'',
              contentImage: storiesList[i].contentImage?storiesList[i].contentImage?.replace("#DH_EMB_IMG_REP#__DHQ_", "448x448_60")?.replace("#DH_EMB_IMG_EXT#","webp"):'',
              deeplinkUrl: storiesList[i].deeplinkUrl,
              groupType: storiesList[i].groupType,
              index: i,
              featuredItems: featuredItems,
              nextPageUrl: nextPageUrl,
              isPlayBtn,
              isShareable,
              imgHeight,
              story:storiesList[i]
            };
            items.push(obj);
          }catch(err){
            console.log("FeaturedStoriesAdapter: Story Reading Error: ",err);
          }
        }
    }  
    }catch(err){
      console.log('FeaturedStoriesAdapter: ',err);
      items = null
    }
    return items;
  }
}

export default FeaturedStoriesAdapter;
